import { Router } from './router/router-module.js';

import { HeaderComponent } from './layout/header-component.js';
import { FooterComponent } from './layout/footer-component.js';
import { SkillsComponent } from './layout/skills-component.js';

Router.addPath('/', '<status-component></status-component');
Router.addPath('/skills', '<skills-component></skills-component');
Router.addPath('/work', '<work-component></work-component');
Router.addPath('/contact', '<contact-component></contact-component');

customElements.define('header-component', HeaderComponent);
customElements.define('footer-component', FooterComponent);
customElements.define('skills-component', SkillsComponent);



